
.select {
  position: relative;
  background: #fff;
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.select .button {
  margin-top: 20px;
  margin-bottom: 20px;
}
.select .room-info-card {
  height: 80px;
  width: 80%;
  background: #fff;
  margin: 10px 0;
  border: 1px solid #168ce0;
  border-radius: 12px;
  padding: 10px;
}
.select .room-name {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.select .flex1 {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.select .flex2 {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
}
.select .flex3 {
  -webkit-box-flex: 3;
      -ms-flex: 3;
          flex: 3;
}
.select .unit {
  margin-left: 10px;
}
.select .check-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.select .checklist {
  padding: 5px 0;
}
.select .delete-btn {
  position: relative;
  left: 85%;
  bottom: 40%;
}
.select h1 {
  font-size: 5vw;
  text-align: center;
}
.select transition {
  width: 100%;
}
.select .result-container {
/*background:yellow;*/
  z-index: 1000;
  width: 80vw;
  padding: 15px;
  border: 1px solid #168ce0;
  border-radius: 15px;
  margin-bottom: 30px;
}
.select .roominfo {
  font-size: 5vw;
  padding: 10px 0;
}
.select .acinfo {
  font-size: 3vw;
  padding: 5px 0;
}
.select .room-total-price {
  color: #168ce0;
  padding: 5px 0;
}
.select .all-price {
  padding: 20px 0;
  border-bottom: 1px solid #808080;
}
.select .scoll-top-btn {
  position: fixed;
  bottom: 50px;
  right: 10px;
}
.select .additional-price {
  padding: 15px 0;
  border-bottom: 1px solid #808080;
}
.select .additional-price span {
  font-size: 5vw;
}
.select .additional-price .additional-price-notice {
  padding: 8px 0;
}
.select .additional-price .additional-price-notice p {
  font-size: 3vw;
  color: #808080;
}
.select .final-price {
  padding: 15px 0;
}
.select .welcome-to-shop {
  padding: 10px 0;
  font-size: 3vw;
}
.select .acw-info {
  padding: 5px 0;
  font-size: 3vw;
}
.select .outdoor-ac-title {
  padding: 10px 0;
  font-size: 5vw;
}
.select .result-title {
  padding: 15px 0;
  color: #168add;
}
.select .change-actype {
  font-size: 3vw;
  text-align: center;
}


