
.menu {
  position: fixed;
  top: 0;
  right: 0;
  color: #fff;
  width: 120px;
  height: 100%;
  background: #3c3c3d;
  z-index: 9;
}
.menu .user {
  height: 60px;
  line-height: 60px;
  background: #000;
  text-align: center;
  width: 60%;
  padding: 0 20%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.menu .aside li {
  height: 50px;
  width: 60%;
  padding: 0 20%;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.menu .aside li a {
  height: 100%;
  color: #fff;
  line-height: 50px;
  display: inline-block;
}
.menu .aside li:hover {
  background-color: #3e90e3;
}


