
.head {
  height: 70px;
  background: #fff;
  position: relative;
  border-bottom: 1px solid;
  border-bottom-color: #d8e8ff;
}
.head img {
  height: 50px;
  position: absolute;
  top: 5px;
  right: 20%;
  cursor: pointer;
}
.head .midea-logo {
  height: 60px;
  top: 5px;
  left: 10px;
}
.head span {
  position: absolute;
  top: 35px;
  left: 170px;
  font-size: 4vw;
  font-weight: bold;
  color: #168add;
}


